/* Import Google Fonts: joan y Nunito */
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Joan&family=Nunito:wght@200&display=swap");

body {
	padding: 0;
	margin: 0;
	background: linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
		linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
		linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
		linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
		linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
		linear-gradient(
			#1d1d1d 25%,
			#1a1a1a 25%,
			#1a1a1a 50%,
			transparent 50%,
			transparent 75%,
			#242424 75%,
			#242424
		);
	background-color: #131313;
	background-size: 20px 20px;
	color: aqua;
}

h1 {
	padding: 0 10px;
}

ul {
	display: flex;
	flex-direction: row;
}

li {
	list-style: none;
}

header {
	padding: 0;
	margin: 0;
}

input {
	height: 22px;
	font-size: 16px;
}

select {
	height: 22px;
	font-size: 16px;
}

.navegacion {
	outline: none;
	text-decoration: none;
	padding: 0 7px;
	font-size: calc(0.5em + 2vw);
}

.font-family-joan {
	font-family: "Joan", serif;
}

.text-success {
	color: green;
}

.text-danger {
	color: red;
}

.text-disable {
	color: rgb(116, 108, 108);
}

.font-family-charis-sil {
	font-family: "Nunito", sans-serif;
}

.d-flex {
	display: flex;
}

.flex-direction-row {
	flex-direction: row;
}

.justify-content-between {
	justify-content: space-between;
}

.justify-content-around {
	justify-content: space-around;
}

.pl-6 {
	padding: 0 8px 0 60px;
}

.pr-6 {
	padding: 0 60px 0 8px;
}

.container {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: 5px auto;
	padding: 10px;
}

.form-input {
	display: flex;
	flex-direction: row;
	padding: 5px;
	margin: 5px;
	align-items: center;
}

.form-row {
	display: flex;
	flex-direction: row;
	padding: 5px;
	margin: 5px;
	align-items: center;
}
.text-blue {
	color: blue;
	font-weight: bold;
}

.primary {
	background-color: blue;
	color: white;
	font-weight: bold;
}

.form-group {
	display: flex;
	flex-direction: column;
}

.logo {
	width: 50px;
	height: 50px;
	padding: 10px;
}

.titulo-h1 {
	padding: 10px;
	margin: 0;
	font-size: 45px;
}

.m-1 {
	margin: 10px;
}

.mx-4 {
	margin: 0 4vw;
}

.p-0 {
	padding: 0;
}

.m-0 {
	margin: 0;
}

.px-4 {
	padding: 0 4vw;
}

.px-2 {
	margin: 0 12px;
}

.p-2 {
	margin: 4px 12px;
}

.vh-100 {
	height: 100vh;
}

.vw-100 {
	width: 100vw;
}

.align-items-end {
	align-content: flex-end;
}

.row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
}

.col-lg-1 {
	width: calc(100% / 12);
}

.col-lg-2 {
	width: calc(2 * 100% / 12);
}

.col-lg-3 {
	width: calc(3 * 100% / 12);
}

.col-lg-4 {
	width: calc(4 * 100% / 12);
}

.col-lg-5 {
	width: calc(5 * 100% / 12);
}

.col-lg-6 {
	width: calc(6 * 100% / 12);
}

.col-lg-7 {
	width: calc(7 * 100% / 12);
}

.col-lg-8 {
	width: calc(8 * 100% / 12);
}

.col-lg-9 {
	width: calc(9 * 100% / 12);
}

.col-lg-10 {
	width: calc(10 * 100% / 12);
}

.col-lg-11 {
	width: calc(11 * 100% / 12);
}

.col-lg-12 {
	width: 100%;
}

.img-fluid {
	width: 100%;
	height: 100%;
}

.img-small {
	width: 100px;
	height: 100px;
	margin: 10px;
}

.bg-dark {
	background-color: rgb(31, 28, 28);
	color: white;
}

.btn {
	height: 36px;
	width: 150px;
	font-size: 15px;
	border-radius: 5px;
	font-weight: bold;
	margin: 0 5px;
	border: none;
	cursor: pointer;
}

.w-90 {
	width: 90%;
}

.w-100 {
	width: 100%;
}

.btn-primary {
	color: wheat;
	background-color: darkcyan;
}

.btn-secondary {
	color: wheat;
	background-color: darkblue;
}

.btn-hover {
	background-color: transparent;
	border: #f9f9f9 2.5px solid;
}

.btn-hover:hover {
	background-color: navy;
	cursor: cell;
	border: #131313;
}

.card {
	width: 80%;
	padding: 2.5px;
	margin: 10px auto;
	border: #151515 2px solid;
}

.card-header {
	background-color: #242424;
	display: flex;
	justify-content: center;
	margin-bottom: 20px;
}

h2 {
	padding: 5px;
}

p {
	padding: 5px;
}

.size-30 {
	width: 30px;
	height: 30px;
}

.m-3 {
	margin: 3px 3pc;
}

.w-40 {
	width: 40%;
}

.m-auto {
	margin: 6px auto;
}

.carrusel {
	width: 500px;
	margin: auto;
	overflow: hidden;
}

.carrusel-inner {
	display: flex;
	flex-direction: row;
	margin: 0;
	padding: 0;
	width: 400%;
	animation: deslizar 6s infinite;
}

.carrusel-item {
	height: 100%;
}

img {
	height: 330px;
	width: 500px;
}

@keyframes deslizar {
	0% {
		margin-left: 0;
	}
	20% {
		margin-left: 0;
	}

	25% {
		margin-left: -102%;
	}
	45% {
		margin-left: -102%;
	}

	50% {
		margin-left: -206%;
	}
	70% {
		margin-left: -206%;
	}

	75% {
		margin-left: -308%;
	}
	100% {
		margin-left: -308%;
	}
}

#menu-bar {
	display: none;
}

header label {
	font-size: 20px;
	color: #f3f5f8;
	cursor: pointer;
	display: none;
}

.card-body {
	border-top: #000 solid 2px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

.text-center {
	text-align: center;
}

.btn:hover {
	filter: saturate(200%);
}

.table {
	text-align: left;
	border-collapse: collapse;
}

th {
	font-weight: normal;
	padding: 15px 5px;
	border-top: #000 3px solid;
}

td {
	font-weight: bold;
	padding: 10px 5px;
	border-top: #000 2px solid;
}

.filtro {
	display: none;
}

.justify-content-center {
	justify-content: center;
}

.w-30 {
	width: 30%;
}

a {
	text-decoration-line: none;
	display: flex;
	justify-content: center;
	align-items: center;
}

@media (max-width: 820px) {
	.container {
		width: 97vw;
		padding: 6px 0;
		margin: 2.5px 0;
		justify-content: center;
		font-size: 3vw;
	}
	.card {
		width: 100%;
		padding: 1px;
		border: none;
	}
	.w-30 {
		width: 60%;
	}
	.card-header {
		justify-content: center;
		justify-content: flex-start;
	}
	.form-input {
		flex-direction: column;
		justify-content: center;
		gap: 10px;
	}
	.name {
		visibility: hidden;
		width: 0%;
	}
	tr {
		display: flex;
		order: 0;
		width: 100%;
	}
	th {
		padding: 15px 2px;
		width: 100%;
		text-align: left;
	}
	td {
		padding: 10px 2px;
		width: 100%;
	}
	.comparte {
		display: none;
	}

	.carrusel {
		margin: 0;
		width: 50vw;
	}
	.carrusel-item {
		height: 100%;
	}
	img {
		height: 30vw;
		width: 50vw;
	}
	@keyframes deslizar {
		0% {
			margin-left: 0;
		}
		20% {
			margin-left: 0;
		}

		25% {
			margin-left: -100%;
		}
		45% {
			margin-left: -100%;
		}

		50% {
			margin-left: -200%;
		}
		70% {
			margin-left: -200%;
		}

		75% {
			margin-left: -300%;
		}
		100% {
			margin-left: -300%;
		}
	}

	header label {
		padding: 0 20px;
		align-self: center;
	}
	header label {
		display: initial;
	}
	header .navbar {
		position: absolute;
		top: 81px;
		left: 0;
		right: 0;
		background: #1a1a1a;
		border-top: 1px solid rgba(0, 0, 0, 0.1);
		display: none;
	}
	header .navbar ul li {
		width: 100%;
		justify-content: flex-end;
	}
	.pl-6 {
		padding: 6px;
	}
	.pl-6 a {
		justify-content: flex-start;
	}
	.pr-6 {
		padding: 6px;
	}
	.pr-6 a {
		justify-content: flex-start;
	}
	.button-group {
		width: 40%;
		display: flex;
		justify-content: flex-end;
	}
	#menu-bar:checked ~ .navbar {
		display: initial;
	}

	.col-sm {
		width: 90%;
		margin: auto;
	}
	.col-sm .card {
		border: 2px solid #131313;
	}
	.col-sm .card .card-header {
		width: 100%;
		display: flex;
		justify-content: center;
	}

	input {
		height: 40px;
		font-size: 30px;
	}
	.btn {
		height: 48px;
		width: 170px;
		font-size: 20px;
		border-radius: 10px;
	}
}

@media (max-width: 1024px) and (min-width: 821px) {
	.col-md-6 {
		width: 50%;
	}

	.carrusel {
		margin: 0;
		width: 400px;
	}
	.carrusel-item {
		height: 100%;
	}
	img {
		height: 33vw;
		width: 250px;
	}
	@keyframes deslizar {
		0% {
			margin-left: 0;
		}
		20% {
			margin-left: 0;
		}

		25% {
			margin-left: -101%;
		}
		45% {
			margin-left: -101%;
		}

		50% {
			margin-left: -202%;
		}
		70% {
			margin-left: -202%;
		}

		75% {
			margin-left: -304%;
		}
		100% {
			margin-left: -304%;
		}
	}
	.pl-6 {
		padding: 6px;
	}
	.pr-6 {
		padding: 6px;
	}
	.logo {
		padding: 0;
	}
	.titulo-h1 {
		padding: 0;
	}
	.justify-content-between {
		justify-content: space-around;
	}
	#registro {
		display: none;
	}
	#inicio_sesion {
		display: none;
	}
}
